@import url('https://fonts.googleapis.com/css?family=Bangers|Abel');

.App {
  text-align: center;
}

.App-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding: 10px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  color: white;
  text-align: left;
  background-image: url("img/Portfolio-blurred.jpg");
  background-size: cover;
  background-position: center;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
}
.App-title{
  font-size: 3em;
}
.App-description{
  background-color: black;
  color: #999;
  padding: 5px 0;
}
a{
  text-decoration: none;
  font-weight: bold;
  color: white;
}
.Form{
    margin-top: 0px;
    background-color: #222;
    display: inline-block;
    border-radius: 5px;
    padding: 10px 20px;
}

.Button{
  cursor: pointer;
  font-size:150%;
  color:white;
  border-radius: 5px;
  padding:10px 20px;
  border: 0;
  background-color: #111;
  font-family: 'Abel', sans-serif;
}

.Input{
  width: 400px;
  font-size:120%;
  color: white;
  background-color:#222;
  border:0;
  border-radius: 5px;
  padding: 10px 10px;
  font-family: 'Abel', sans-serif;
}



